import * as React from "react"
import SEO from "../../components/seo";
import SyntaxHighlight from "../../components/SynaxHighlight";

const Maps = () => {
    return (
        <>
            <SEO title={ "Maps" }/>
            <h1>Maps</h1>
            <p>Maps are used to generate themes and utility classes.</p>
            <h2>Add to map</h2>
            <p>You can add values to a map with the map-merge function. First you @use the maps file and then you overwrite the map on the file that you want to use,</p>
            <p>In the example bellow we will add a color to the themes.</p>
            <SyntaxHighlight lang={ "scss" } codeString={ `//First import the sass function
@use "sass:color";

//Then import the maps from the library
@use "~skil3e-css/src/maps";

//Finally to merge the values to the map on the file you want.
@use "~skil3e-css/src/basic/themes" with (
    $themeLight: map-merge(maps.$themeLight, ("bg2": #eeeeee)),
    $themeDark: map-merge(maps.$themeDark, ("bg2": #111111))    
);` }
            />
            <p>You can edit the following maps.</p>

            <SyntaxHighlight lang={ "scss" } codeString={`@use "globals";
//--------------------------------------------------------------//
//  Align / Justify
//  Borders
//  Breakpoints
//  Colors
//  Cursor
//  Display
//  Object Fit
//  Positions
//  Spacers
//  Style Attribute
//  Themes
//  Typography
//  Width
//  Z-index
//--------------------------------------------------------------//

//--------------------------------------------------------------//
//  Align
//--------------------------------------------------------------//
$align-item       : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
\t'stretch' : stretch,
);

$align-content    : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
\t'stretch' : stretch,
);

$align-self       : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
);

$justify-item     : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
\t'stretch' : stretch,
);

$justify-content  : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
\t'stretch' : stretch,
\t'between' : space-between,
\t'around' : space-around,
\t'evenly': space-evenly
);

$justify-self     : (
\t'center' : center,
\t'start' : start,
\t'end' : end,
\t'stretch' : stretch
);
//--------------------------------------------------------------//
//  Borders
//--------------------------------------------------------------//
$border-widths    : (
\t"--0": 0,
\t"": 1px,
\t"--md": 2px,
\t"--lg": 3px,
);

$border-radius    : (
\t"": .25rem,
\t'--0' : 0,
\t"--md": .625rem,
\t"--lg": .9375rem,
\t"--circle": 1000rem
);
//--------------------------------------------------------------//
//  Breakpoints
//--------------------------------------------------------------//
$breakpoints      : (
\t'sm__': globals.$phone,
\t'md__': globals.$tablet,
\t'lg__': globals.$laptop,
\t'xl__': globals.$desktop,
);
//--------------------------------------------------------------//
//  Colors
//--------------------------------------------------------------//
$colors           : (
\t'primary' : globals.$primary,
\t'danger' : globals.$danger,
\t'success' : globals.$success,
\t'black' : globals.$black,
\t'white' : globals.$white
);
//--------------------------------------------------------------//
//  Cursor
//--------------------------------------------------------------//
$cursors-basic    : (
\t"auto": auto,
\t"grab": grab,
\t"grabbing": grabbing,
\t"none": none,
\t"not-allowed": not-allowed,
\t"pointer": pointer,
);

$cursors-extended : (
\t"alias": alias,
\t"all-scroll": all-scroll,
\t"auto": auto,
\t"cell": cell,
\t"context-menu": ontext-menu,
\t"col-resize": col-resize,
\t"copy": copy,
\t"crosshair": crosshair,
\t"default": default,
\t"e-resize": e-resize,
\t"ew-resize": ew-resize,
\t"grab": grab,
\t"grabbing": grabbing,
\t"help": help,
\t"move": move,
\t"n-resize": n-resize,
\t"ne-resize": ne-resize,
\t"nesw-resize": nesw-resize,
\t"ns-resize": ns-resize,
\t"nw-resize": nw-resize,
\t"nwse-resize": nwse-resize,
\t"no-drop": no-drop,
\t"none": none,
\t"not-allowed": not-allowed,
\t"pointer": pointer,
\t"progress": progress,
\t"row-resize": row-resize,
\t"s-resize": s-resize,
\t"se-resize": se-resize,
\t"sw-resize": sw-resize,
\t"text": text,
\t"w-resize": w-resize,
\t"wait": wait,
\t"zoom-in": zoom-in,
\t"zoom-out": zoom-out,
);
//--------------------------------------------------------------//
//  Display
//--------------------------------------------------------------//
$display          : (
\t'none' : none,
\t'block' : block,
\t'inline' : inline,
\t'inline-block': inline-block,
\t'table' : table,
\t'table-cell' : table-cell,
\t'table-row' : table-row,
\t'flow-root' : flow-root,
\t'flex' : flex,
\t'inline-flex' : inline-flex,
\t'grid': grid,
\t'inline-grid': inline-grid,
);
//--------------------------------------------------------------//
//  Object Fit
//--------------------------------------------------------------//
$objectFit        : (
\t'contain' : contain,
\t'cover' : cover,
\t'fill' : fill,
\t'none' : none,
\t'scale-down' : scale-down
);
$object-position  : (
\t'center' : center,
\t'top' : top,
\t'bottom' : bottom,
\t'left' : left,
\t'right' : right,
\t'left-top' : left top,
\t'left-bottom' : left bottom,
\t'right-top' : right top,
\t'right-bottom' : right bottom,
);
//--------------------------------------------------------------//
//  Positions
//--------------------------------------------------------------//
$positions        : (
\t'static' : static,
\t'relative': relative,
\t'fixed' : fixed,
\t'absolute' : absolute,
\t'sticky': sticky
);
$positions-values : (
\t'0' : 0,
\t'auto': auto
);
//--------------------------------------------------------------//
//  Spacers
//--------------------------------------------------------------//
$spacers          : (
\t'0' : 0,
\t'10' : 0.625rem, // 10px
\t'15' : 0.9375rem, // 15px
\t'xs' : 0.375rem, // 6px
\t'sm' : 0.75rem, // 12px
\t'md' : 0.9375rem, // 15px
\t'lg' : 1.3125rem, // 21px
\t'xl' : 1.875rem, // 30px
\t'xxl': 2.5rem, // 40px
);
//--------------------------------------------------------------//
// Style Attribute
//--------------------------------------------------------------//
$style-attribute  : (
\t'text' : color,
\t'bg' : background-color,
\t'fill' : fill,
\t'brd' : border-color
);
//--------------------------------------------------------------//
//  Themes
//--------------------------------------------------------------//
$themeLight       : (
\t'body' : globals.$gray-95,
\t'bg' : globals.$gray-90,
\t'bgDimmed' : globals.$gray-85,
\t'border' : globals.$gray-80,
\t'text' : globals.$gray-10,
\t'textDimmed' : globals.$gray-60,
\t'accent' : globals.$primary,
);

$themeDark        : (
\t'body' : globals.$gray-5,
\t'bg' : globals.$gray-10,
\t'bgDimmed' : globals.$gray-15,
\t'border' : globals.$gray-20,
\t'text' : globals.$gray-90,
\t'textDimmed' : globals.$gray-40,
\t'accent' : globals.$primary,
);
//--------------------------------------------------------------//
//  Typography
//--------------------------------------------------------------//
$typography       : (
\t'small': globals.$small,
\t'h6' : globals.$h6,
\t'h5' : globals.$h5,
\t'h4' : globals.$h4,
\t'h3' : globals.$h3,
\t'h2' : globals.$h2,
\t'h1' : globals.$h1
);

$font-sizes       : (
\t'xl-1': 3rem,
\t'xl-2': 4rem,
\t'xl-3': 6rem,
);
//--------------------------------------------------------------//
// Width
//--------------------------------------------------------------//
$width            : (
\t'100' : 100%,
\t'75' : 75%,
\t'50' : 50%,
\t'25' : 25%
);
$vWidth            : (
\t'100' : 100vh,
\t'75' : 75vh,
\t'50' : 50vh,
\t'25' : 25vh
);
$height           : (
\t'100' : 100%,
\t'75' : 75%,
\t'50' : 50%,
\t'25' : 25%
);
$vHeight           : (
\t'100' : 100vh,
\t'75' : 75vh,
\t'50' : 50vh,
\t'25' : 25vh
);
//--------------------------------------------------------------//
//  Z-index
//--------------------------------------------------------------//
$z-index          : (
\t'1': 1000,
\t'2': 2000,
\t'3': 3000,
\t'4': 4000,
\t'5': 5000,
\t'6': 6000,
\t'7': 7000,
\t'8': 8000,
\t'9': 9000,
);
`}
            />
        </>
    )
}

export default Maps
